@use'hero';
@use'componets/infos_bar';
@use'componets/buttons';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto, sans-serif;
    color: #fff;
    list-style: none;
}

.container {
    max-width: 960px;
    width: 100%;
    margin: 0 auto;

    @media (max-width: 640px) {
        width: 80%;
    }

    @media (max-width: 1024px) {
        width: 90px;
    }
}